.character-list {
  list-style: none;
  text-decoration: none;
  height: 100%;
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 1fr;
  @media all and (min-width: 768px) {
    grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
  }
}
.character-link {
  text-decoration: none;
  color: black;
}
